
import { mapState, mapActions } from 'vuex'

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
Blur,
Flexbox,
FlexboxItem,
Cell,
Card,
Group,
Radio,
Divider,
XButton
},
async mounted () {
let ret = await this.getQuestion()
if(!ret) {
this.$vux.alert.show({
content: `问题不存在`
})
return this.$router.go(-1)
}
},
computed: {
...mapState({
AskInfo: state => state.user.Check.AskInfo,
Groups: state => state.user.Check.Groups
}),
total  () {
let total = 0
this.Groups.map(g => {
total += g.fen
})
return total
}
},
filters: {},
methods: {
...mapActions(['getQuestion',"complateGroupFen","submitCheck"]),
changeRadio (a , b) {
let aArray = a.split('_')
let groupId = aArray[0]
let selectId = a
let questioId = aArray[0] +"_" +aArray[1]
this.complateGroupFen({groupId,questioId,selectId})
},
async submitInfo () {


for(let i =0; i < this.Groups.length; i++) {
let group = this.Groups[i]
for(let j = 0; j < group.questions.length; j++) {
let q = group.questions[j]
if(!q.select) {
return this.$vux.alert.show({
content: `第${group.index}组的第${q.index}题没有填写`
})
}
}
}

let ret = await this.submitCheck()
this.$router.push({name: 'AlertCheck', params: {ret}})
}
},
data () {
return {





url: 'static/images/logo.jpg',
background: 'static/images/bg.jpg'
}
}
}



